'use strict';

var React = require('react/addons');
var $ = require('jquery');

var JournalOrderForm = React.createClass({displayName: "JournalOrderForm",
  mixins: [React.addons.LinkedStateMixin],
  getInitialState: function(){
    return {
      showOrderForm:'hideOrderForm',
      buttonTitle: 'Anmod om innsyn',
      innsynBestilt: '',
      bestillernavn:'',
      bestillermail:'',
      bestillertelefon:'',
      bestillerorganisasjon:'',
      bestillerland:'Norge',
      bestilleraddress:'',
      bestillerzipcode:'',
      bestillercity:'',
      errormsg:''
    }
  },
  buttonHandler: function(e){
    var newState = this.state.showOrderForm === 'hideOrderForm' ? 'showOrderForm' : 'hideOrderForm';
    var newTitle = this.state.buttonTitle === 'Avbryt' ? 'Anmod om innsyn' : 'Avbryt';
    this.setState({showOrderForm:newState, buttonTitle: newTitle});
  },
  orderHandler: function(e){
    e.preventDefault();
    var that = this;
    var payload = {
      navn: this.state.bestillernavn,
      organisasjon: this.state.bestillerorganisasjon,
      land: this.state.bestillerland,
      telefon: this.state.bestillertelefon,
      epost: this.state.bestillermail,
      adresse: this.state.bestilleraddress,
      postnr: this.state.bestillerzipcode,
      poststed: this.state.bestillercity,
      enhet: this.props.journal.JOURNPOST_OJ.JP_ANSVAVD,
      saksnr: this.props.journal.SA_SAKNR,
      dokumentnr: this.props.journal.JOURNPOST_OJ.JP_DOKNR.split('-')[1],
      journalnr: this.props.journal.JOURNPOST_OJ.JP_SEKNR + '/' + this.props.journal.JOURNPOST_OJ.JP_JDATO.toString().substr(2,2)
    };

    $.get('https://app.t-fk.no/innsyn.php', payload, function(data){
      if(data.results.errorcode === '0'){
        that.setState({
          showOrderForm:'hideOrderForm',
          buttonTitle: 'Anmodning om innsyn sendt',
          bestillernavn:'',
          bestillermail:'',
          bestillertelefon:'',
          bestillerorganisasjon:'',
          bestillerland:'',
          bestilleraddress:'',
          bestillerzipcode:'',
          bestillercity:''
        });
      } else {
        var errormsg = 'Noe gikk galt, vennligst kontakt arkivet på <a href="mailto:post.arkiv@t-fk.no">post.arkiv@t-fk.no</a>';
        that.setState({
          showOrderForm:'hideOrderForm',
          buttonTitle: 'Anmod om innsyn',
          errormsg: errormsg
        });
      }
    });

  },
  render: function(){
    return (
      React.createElement("div", null, 
        React.createElement("button", {className: "button--secondary", onClick: this.buttonHandler}, this.state.buttonTitle), React.createElement("span", {className: "orderFormError"}, this.state.errormsg), 
        React.createElement("div", {className: this.state.showOrderForm}, 
          React.createElement("h3", null, "Anmodning om innsyn"), 
          React.createElement("form", {onSubmit: this.orderHandler}, 
            React.createElement("fieldset", null, 
              React.createElement("legend", null, "Dokument: ", this.props.journal.JOURNPOST_OJ.JP_DOKNR, " ", this.props.journal.JOURNPOST_OJ.JP_OFFINNHOLD), 
              React.createElement("label", {htmlFor: "bestillernavn"}, "Navn: "), 
              React.createElement("input", {type: "text", name: "bestillernavn", placeholder: "Fullt navn", autoComplete: "name", valueLink: this.linkState('bestillernavn')}), 
              React.createElement("label", {htmlFor: "bestilleorganisasjon"}, "Organisasjon: "), 
              React.createElement("input", {type: "text", name: "bestillerorganisasjon", placeholder: "Organisasjon", autoComplete: "organization", valueLink: this.linkState('bestillerorganisasjon')}), 
              React.createElement("label", {htmlFor: "bestilleorland"}, "Land: "), 
              React.createElement("input", {type: "text", name: "bestillerland", placeholder: "Land", autoComplete: "country", valueLink: this.linkState('bestillerland')}), 
              React.createElement("label", {htmlFor: "bestillermail"}, "E-post: "), 
              React.createElement("input", {type: "text", name: "bestillermail", placeholder: "E-post", autoComplete: "email", valueLink: this.linkState('bestillermail')}), 
              React.createElement("label", {htmlFor: "bestillertelefon"}, "Telefon: "), 
              React.createElement("input", {type: "text", name: "bestillertelefon", placeholder: "Telefon", autoComplete: "phone", valueLink: this.linkState('bestillertelefon')}), 
              React.createElement("p", null, 
                "Feltene for postadresse fylles kun ut dersom du ønsker å motta dokumentene pr post"
              ), 
              React.createElement("label", {htmlFor: "bestilleraddress"}, "Postadresse: "), 
              React.createElement("input", {type: "text", name: "bestilleraddress", placeholder: "Postadresse", autoComplete: "address", valueLink: this.linkState('bestilleraddress')}), 
              React.createElement("label", {htmlFor: "bestillerzipcode"}, "Postnummer: "), 
              React.createElement("input", {type: "text", name: "bestillerzipcode", placeholder: "Postnummer", autoComplete: "zipcode", valueLink: this.linkState('bestillerzipcode')}), 
              React.createElement("label", {htmlFor: "bestillercity"}, "Poststed: "), 
              React.createElement("input", {type: "text", name: "bestillercity", placeholder: "Poststed", autoComplete: "city", valueLink: this.linkState('bestillercity')})
            )
          ), 
          React.createElement("button", {className: "button--primary", onClick: this.orderHandler}, "Send anmoding"), " ", React.createElement("button", {className: "button--secondary", onClick: this.buttonHandler}, "Avbryt")

        )
      )
    )
  }
});

module.exports = JournalOrderForm;